import './style.css';

import React from 'react';
import { render } from 'react-dom';

import Feature from '../Feature';

class FeatureList extends React.Component {

    constructor(props){

        super(props);

        this.state = {
            openFeatureId: null,
        }
    }

    render() {

        const {features} = this.props;
        const featureElements = features.map((feature, key) => {
            return <li key={key}>
                        <Feature feature={feature} isValueShown={this.state.openFeatureId === key} onFeatureNameClick={this.showValues.bind(this, key)}/>
                    </li>;
        });
    
        return(
            <ul id="feature-list">
                {featureElements}
            </ul>
        );
    }

    showValues(openFeatureId) {

        this.setState({
            openFeatureId: this.state.openFeatureId === openFeatureId ? null : openFeatureId,
        });
    }
}

export default FeatureList